import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/Home/HomeView.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/classification',
    name: 'classification',
    meta: { showSearchBar: true },
    component: () => import('../views/Classification/ClassificationView.vue')
  },
  {
    path: '/miquan',
    name: 'miquan',
    component: () => import('../views/Mi/MiQuan.vue')
  },
  {
    path: '/car',
    name: 'car',
    meta: { needLogin: true },
    component: () => import('../views/Car/CarView.vue')
  },
  {
    path: '/my',
    name: 'my',
    meta: { needLogin: true },
    component: () => import('../views/My/MyView.vue')
  },
  {
    path: '/search',
    name: 'search',
    meta: { hideNav: true },
    component: () => import('../views/Search/SearchView.vue'),
  },
  {
    path: '/searchlist',
    name: 'searchlist',
    meta: { hideNav: true },
    component: () => import('../views/Search/SearchResultView.vue'),
  },
  // 产品详情
  {
    path: '/productdetails',
    name: 'productdetails',
    meta: { hideNav: true },
    component: () => import('../views/Product/ProductDetails.vue'),
  },
  // 米圈问题详情
  {
    path: '/problemdetail',
    name: 'problemdetail',
    meta: { hideNav: true },
    component: () => import('../views/Product/ProblemDetails.vue'),
  },
  // 产品米圈页
  {
    path: '/miquanproduct',
    name: 'miquanproduct',
    meta: { hideNav: true },
    component: () => import('../views/Product/ProductCommit.vue')
  },
  {
    path: '/micomment',
    name: 'micomment',
    meta: { hideNav: true },
    component: () => import('../views/Mi/MiComment.vue')
  },
  // 米圈推荐详情
  {
    path: '/recommendeddetails',
    name: 'recommendeddetails',
    meta: { hideNav: true },
    component: () => import('../views/Product/RecommendedDetails.vue')
  },
  // 米圈测评详情
  {
    path: '/videodetails',
    name: 'videodetails',
    meta: { hideNav: true },
    component: () => import('../views/Product/VideoDetails.vue')
  },
  // 米圈测评详情
  {
    path: '/shopdetails',
    name: 'shopdetails',
    meta: { hideNav: true },
    component: () => import('../views/Product/ShopDetails.vue')
  },
  // 分类列表
  {
    path: '/categorylist',
    name: 'categorylist',
    meta: { hideNav: true },
    component: () => import('../views/Classification/CategoryList.vue'),
  },
  {
    path: '/login',
    name: 'login',
    meta: { hideNav: true },
    component: () => import('../views/Login/LoginView.vue'),
  },
  {
    path: '/register',
    name: 'register',
    meta: { hideNav: true },
    component: () => import('../views/Login/RegisterView.vue'),
  },
  {
    path: '/buypage',
    name: 'buypage',
    meta: { hideNav: true },
    component: () => import('../views/BuyPage/BuyPage.vue'),
  },
  {
    path: '/orderdetail',
    name: 'orderdetail',
    meta: { hideNav: true },
    component: () => import('../views/BuyPage/OrderDetail.vue'),
  },
  {
    path: '/orderpay',
    name: 'orderpay',
    meta: { hideNav: true },
    component: () => import('../views/BuyPage/OrderPay.vue'),
  },
  {
    path: '/allbuylist',
    name: 'allbuylist',
    meta: { hideNav: true },
    component: () => import('../views/BuyPage/AllBuyList.vue'),
  },
  {
    path: '/completelist',
    name: 'completelist',
    meta: { hideNav: true },
    component: () => import('../views/BuyPage/CompleteList.vue'),
  }
]
const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.needLogin && !store.state.token) {
    router.push({ path: '/login',query:{target:to.fullPath} })
  } else {
    next()
  }
})

export default router
