import Vue from 'vue'
import Vuex from 'vuex'
import { instance, search } from '@/utils/axios.js';
// import '@/utils/axios.js';


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 首页
    pageInfo: null,
    // 分类
    categoryList: null,
    // 产品数据
    productDetail: null,
    //米圈评论
    contentList: null,
    // 用户信息
    token: localStorage.getItem('token') || '',
    // 购物车
    carList: JSON.parse(localStorage.getItem('carList')) || [],
    // 订单
    orderList: JSON.parse(localStorage.getItem('orderList')) || []

  },
  getters: {

  },
  mutations: {
    // 更改首页数据
    upadtePageInfo(state, page) {
      state.pageInfo = page
    },
    // 更改分类数据
    upadteCategoryList(state, list) {
      state.categoryList = list
      console.log("upadteCategoryList", state.categoryList);
    },
    upadteProductDetail(state, list) {
      state.productDetail = list
      console.log("upadteProductDetail", state.productDetail);
    },
    delProductDetail(state) {
      state.productDetail = null
      console.log("upadteProductDetail", state.productDetail);
    },
    // 更新米圈评论
    upadteContentList(state, list) {
      state.contentList = list
      console.log("contentList", state.contentList);
    },
    // 登录
    userLogin(state, playLoad) {
      state.token = playLoad
    },
    // 更新购物车列表
    updateCar(state) {
      // state.carList = JSON.parse(localStorage.getItem('carList'))
      const storedCarList = localStorage.getItem('carList');
      if (storedCarList) {
        state.carList = JSON.parse(storedCarList);
      }
    },
    // 更新订单列表
    updateOrder(state) {
      state.orderList = JSON.parse(localStorage.getItem('orderList'))
    },
    // 退出登录
    setToken(state) {
      state.token = ''
    }
  },
  actions: {
    // 获取分类数据
    getCategoryList({ commit }, id) {
      instance
        .get(`product/all_product?cat_id=${id}`)
        .then((res) => {
          commit('upadteCategoryList', res.data.data);
        });
    },

    // 获取产品信息
    getProductDetail({ commit }, id) {
      search
        .get(`miproduct/view?commodity_id=${id}`)
        .then((res) => {
          commit('upadteProductDetail', res.data.data);
        });
    },
  },
  modules: {
  }
})
