<template>
  <div class="home">
    <header>
      <!-- 搜索框 -->
      <van-search
        placeholder="请输入搜索关键词"
        @click="$router.push({ path: '/search' })"
      />
    </header>
    <van-tabs sticky animated @click="getPages">
      <van-tab v-for="tab in Tabs" :key="tab.page_id" :title="tab.name">
        <!-- 主体部分 -->
        <IntelligenceView />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import IntelligenceView from "@/components/Home/IntelligenceView.vue";
import { mapState, mapMutations } from "vuex";

export default {
  name: "HomeView",
  components: {
    IntelligenceView,
  },
  data() {
    return {
      // Tabs列表
      Tabs: null,
    };
  },
  computed: {
    ...mapState(["pageInfo"]),
  },
  methods: {
    ...mapMutations(["upadtePageInfo"]),
    getPages(index) {
      if (index === 0) {
        this.axios.get("home/page").then((res) => {
          this.Tabs = res.data.data.tabs;
          // 页面数据
          this.upadtePageInfo(res.data.data.data.sections);
        });
        console.log(this.pageInfo);
      } else {
        const id = this.Tabs[index].page_id;
        console.log(id, index);
        this.axios
          .get("home/page?page_type=activity&page_id=" + id)
          .then((res) => {
            this.Tabs = res.data.data.tabs;
            // 页面数据
            this.upadtePageInfo(res.data.data.data.sections);
          });
        console.log(this.pageInfo);
      }
    },
  },
  created() {
    this.getPages(0);
  },
};
</script>
<style lang="less" scoped>
header {
  width: 100vw;
  background: #ffffff61;
  backdrop-filter: blur(40px);
  z-index: 2;
  .van-search {
    padding: 20px;
    background: transparent;
  }
}
</style>