          path: '/productdetails',
          query: { product_id: lis.action.path },
        })
      "
    >
      <img :src="lis.img_url" alt="" />
      <h4 class="name text-ellipsis">{{ lis.product_name }}</h4>
      <p class="brief text-ellipsis">{{ lis.product_brief }}</p>
      <p class="price">￥{{ lis.show_price }}</p>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["item"],
};
</script>

