ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  li {
    width: 32vw;
    // margin: 4px 0;
    img {
      width: 100%;
    }
    .name {
      margin: 8px;
      font-size: 12px;
    }
    .brief {
      font-size: 12px;
      transform: scale(0.85);
    }
    .price {
      color: red;
      margin: 4px;
    }
    .text-ellipsis {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      padding: 4px;
